*, *:before, *:after {
    box-sizing: inherit;
  }

  html {
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
    color: #181717;
  }

  
  a:link, a:visited, a:active  {
    text-decoration: none; 
    color: #181717;
    font-weight: bold;
    padding: 1rem;
  }

  a:hover  {
    color: #FC5050;
  }

  .header {
    margin-top: 20px;
    padding: 1rem;
    width: 100%;
    height: 25rem;
    background-color: #eeeeee ;
  }
  
 
/*TOP NAVIGATION*/
.topNav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}


.right {
  display:inline-flex;
}

.left {
  display: inline-flex;
}

/*BOTTOM NAVIGATION*/
  .bottomNav {
    letter-spacing: 0.1em; 
    background-color:#eeeeee;
    
  }
  
   ul {
    margin: auto;
    padding:0;
    text-align:center;
    /*display: flex;
    justify-content: flex-end;*/
  }

  ul li {
    display: inline;   /* arranges link items horizontally */
  }

  .item {
    font-size: 25px;
    display: inline-block;
  }

  /*RESPONSIVE NAVIGATION*/
  @media all and (max-width: 800px) {
    .item{
      font-size: 20px;
      padding: 10px;
    }

  }

  @media all and (max-width: 635px) {
    .item{
      font-size: 18px;
      padding: 10px;
    }

  }

  @media all and (max-width: 600px) {
    .item{
      font-size: 18px;
      padding: 10px;
    }
    ul li {
      padding: 0px;
    }
  }

  @media all and (max-width: 440px) {
    .item{
      font-size: 12px;
    }
    ul li {
      padding: 0px;
    }
  }

  @media all and (max-width: 330px) {
    .item{
      font-size: 10px;
    }
    ul li {
      padding: 0px;
    }
  }
  


